
@import url("https://fonts.googleapis.com/css?family=Poppins:300,400,500,600,700|Raleway:400,700");
body {
  width: 100%;
  min-height: 100vh;
  padding: 0;
  margin: 0;
  background-color: #F6F6F6;
  font-size: 16px;
  font-family: "Poppins";
  color: #161616;
  -webkit-font-smoothing: antialiased;
  position: relative;
}
@media (min-width: 800px) {
body {
      font-size: 18px;
}
}
* {
  -webkit-tap-highlight-color: transparent;
}
*:focus {
  outline: none;
}
ul {
  list-style: none;
  margin: 0;
  padding: 0;
}
a {
  text-decoration: none;
}
h1 {
  font-weight: 600;
  margin: 0;
}
h2 {
  margin: 0;
}
.Bg-Lines {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  overflow: hidden;
  z-index: 2;
}
.Bg-Lines.hasColor-clearcanvas .Bg-Line::before {
    -webkit-transform: translateX(0);
            transform: translateX(0);
    background-color: #00b2ec;
    -webkit-transition-delay: 0s;
            transition-delay: 0s;
}
.Bg-Lines.hasColor-taxifusion .Bg-Line::before {
    -webkit-transform: translateX(0);
            transform: translateX(0);
    background-color: #FFBE00;
}
.Bg-Lines.hasColor-lumencase .Bg-Line::before {
    -webkit-transform: translateX(0);
            transform: translateX(0);
    background-color: #F6416C;
}
.Bg-Lines.hasColor-slected .Bg-Line::before {
    -webkit-transform: translateX(0);
            transform: translateX(0);
    background-color: #1852A4;
}
.Bg-Line {
  position: absolute;
  top: 0;
  width: 1px;
  height: 100%;
  z-index: 1;
}
.Bg-Line {
  overflow: hidden;
  width: calc(12.5vw + 1px);
}
.Bg-Line::before {
    position: absolute;
    top: 0;
    -webkit-transform: translateX(-12.5vw);
            transform: translateX(-12.5vw);
    content: "";
    width: calc(12.5vw - 0px);
    height: 100%;
    -webkit-transition: all 0.5s ease;
    transition: all 0.5s ease;
    -webkit-transition-delay: 0.3s;
            transition-delay: 0.3s;
}
.Bg-Line::after {
    position: absolute;
    left: 0;
    top: 0;
    content: "";
    height: 100%;
    width: 1px;
    background-color: rgba(81, 33, 230, 0.1);
}
.Bg-Line .Bg-Line-Animation {
    position: absolute;
    width: 3px;
    left: -1px;
    height: 140px;
    z-index: 99;
    overflow: hidden;
}
.Bg-Line .Bg-Line-Animation::before {
      position: absolute;
      content: "";
      -webkit-transform: translateY(-100%);
              transform: translateY(-100%);
      width: 100%;
      height: 50px;
      top: 0;
      background-color: #F97794;
      -webkit-animation-name: lineanimation;
              animation-name: lineanimation;
      -webkit-animation-duration: 4s;
              animation-duration: 4s;
      -webkit-animation-iteration-count: 2;
              animation-iteration-count: 2;
      z-index: 99999;
}
.Bg-Line-2 .Bg-Line-Animation {
  top: 20%;
}
.Bg-Line-2 .Bg-Line-Animation::before {
    height: 80px;
}
.Bg-Line-3 .Bg-Line-Animation {
  top: 30%;
}
.Bg-Line-3 .Bg-Line-Animation::before {
    height: 150px;
    -webkit-animation-delay: 1.5s;
            animation-delay: 1.5s;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
}
.Bg-Line-4 .Bg-Line-Animation {
  top: 10%;
}
.Bg-Line-4 .Bg-Line-Animation::before {
    -webkit-animation-delay: 3.5s;
            animation-delay: 3.5s;
    -webkit-animation-timing-function: ease;
            animation-timing-function: ease;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
    height: 70px;
}
.Bg-Line-5 .Bg-Line-Animation {
  top: 85%;
}
.Bg-Line-5 .Bg-Line-Animation::before {
    -webkit-animation-delay: 2s;
            animation-delay: 2s;
}
.Bg-Line-7 .Bg-Line-Animation {
  top: 50%;
}
.Bg-Line-7 .Bg-Line-Animation::before {
    height: 120px;
    -webkit-animation-delay: 1s;
            animation-delay: 1s;
}
.Bg-Line-8 .Bg-Line-Animation {
  top: 50%;
}
.Bg-Line-8 .Bg-Line-Animation::before {
    -webkit-animation-delay: 5s;
            animation-delay: 5s;
    -webkit-animation-timing-function: ease;
            animation-timing-function: ease;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
    height: 50px;
}
@-webkit-keyframes lineanimation {
100% {
    -webkit-transform: translateY(500%);
            transform: translateY(500%);
}
}
@keyframes lineanimation {
100% {
    -webkit-transform: translateY(500%);
            transform: translateY(500%);
}
}
.Bg-Line:nth-child(0) {
  left: calc((0 - 1 ) * 12.5vw - 1px);
}
.Bg-Line:nth-child(1) {
  left: calc((1 - 1 ) * 12.5vw - 1px);
}
.Bg-Line:nth-child(2) {
  left: calc((2 - 1 ) * 12.5vw - 1px);
}
.Bg-Line:nth-child(3) {
  left: calc((3 - 1 ) * 12.5vw - 1px);
}
.Bg-Line:nth-child(4) {
  left: calc((4 - 1 ) * 12.5vw - 1px);
}
.Bg-Line:nth-child(5) {
  left: calc((5 - 1 ) * 12.5vw - 1px);
}
.Bg-Line:nth-child(6) {
  left: calc((6 - 1 ) * 12.5vw - 1px);
}
.Bg-Line:nth-child(7) {
  left: calc((7 - 1 ) * 12.5vw - 1px);
}
.Bg-Line:nth-child(8) {
  left: calc((8 - 1 ) * 12.5vw - 1px);
}



